import React, { Component } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'

class Login extends Component {

    constructor(props){
        super(props)
        this.state = {
            'username': 'admin',
            'password': "root"
        }
    }

    handleInputChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value,
        })
    }

    handleSubmit = (e) => {
        e.preventDefault();
        const { username, password } = this.state;
        console.log(username)
        axios.post('/api/adminLogin', {
            username: username,
            password: password,
        })
        .then(function(response){ 
            if(response.data){
                window.location.assign("/newelection")
            }else{
                alert('Incorrect Username or Password');
            }
        })
        .catch(function(err){
            console.error(err);
        });
    }


    render(){
        return(
            <div className="container">
                <form onSubmit={this.handleSubmit}>
                    <input type="text" id="username" name="username" onChange={this.handleInputChange} required/>
                    <label htmlFor="name">Username</label><br></br>
                    <input type="password" id="password" name="password" onChange={this.handleInputChange} required/>
                    <label htmlFor="name">Password</label><br></br><br></br>
                    <Link to={"/main"} className="title"><button className="waves-effect waves-light btn yellow darken-3">Login</button></Link>
               </form>
            </div>      
        )
    }
}

export default Login;