import React, { Component } from 'react';
import axios from 'axios';

class NewElection extends Component {

    constructor(props) {
        super(props);
        this.state = {
            election_name: '',
            election_organizer: '',
            election_password: '',
        };
    }

    handleInputChange = e => {
        this.setState({
        [e.target.name]: e.target.value,
        });
    };

    handleSubmit = (e) => {
        e.preventDefault();
        const { election_name, election_organizer, election_position } = this.state;
        console.log(election_name);
        axios.post('/api/electionName', {
            election_name: election_name,
            election_organizer: election_organizer,
            election_position: election_position
        })
        .then(function(response){ 
            window.location.assign('/elections');
        })
        .catch(function(err){
            console.error(err);
        });
    }

    render(){
        return(
            <div className="container">
                <h4>Create New Election</h4>
                    <form onSubmit={this.handleSubmit}>
                        <input type="text" id="election_name" name="election_name" onChange={this.handleInputChange} required/>
                        <label htmlFor="name">Election Name</label><br></br>

                        <input type="text" id="election_organizer" name="election_organizer" onChange={this.handleInputChange} required/>
                        <label htmlFor="name">Election Organizer</label><br></br>

                        <input type="text" id="election_position" name="election_position" onChange={this.handleInputChange} required/>
                        <label htmlFor="name">Position In ELection</label><br></br>
                        
                        

                        <button className="btn blue darken-2" type="submit" name="action">Submit
                            <i className="material-icons right">send</i>
                        </button>
                    </form>
            </div>
        )
    }
}

export default NewElection;